@import '../styles/mixins';

.section-content{

    .content-wrapper{
        display: flex;
        flex:1;
        flex-direction: column;
        opacity: 0;
        transition: opacity 0.8s ease-in;

        &.show {
            opacity: 1;
        }

        &.text-right{
            text-align: right;
        }
        &.text-left{
            text-align: left;
        }

        .heading{
            font-weight: 700;
            font-size: 4rem;
            color:#414141;
            padding-top: 1rem;
            padding-bottom: 1rem;

            @include respond(tab-port){
                text-align: center;
            }
        }

        .text {
            font-size: 2rem;
            font-weight: 400;
            color: #888;

            @include respond(tab-port){
                text-align: center;
            }
        }

        .link {
          color: #414141;
        }

        .imgWrapper {
            flex:1;
            align-items:center;
            justify-content: center;
            width: 400px;
            height: 400px;
            margin:0 auto;

            @include respond(tab-port){
                margin-bottom: 2rem; // add space between text and image
                width: 18rem;
                height: auto;
            }

            .img{
                width: 100%;
                height:100%;
                object-fit: contain;
            }

            &.small{
                width: 300px;

                @include respond(tab-port){
                    width: 20rem;
                }
            }
        }
    }

    // manages visibility of extract component as two text content components are added
    @media only screen and (max-width: 600px) {
        .hide-on-small-and-down {
            display: none !important;
        }
    }
    @media only screen and (min-width: 601px) {
        .hide-on-med-and-up {
            display: none !important;
        }
    }
}
// media query for section centent which manages text alignment for web text
@media only screen and (max-width: 600px) {
    .section-content {
        display: block !important;
    }
}
@media only screen and (min-width: 601px) {
    .section-content {
        display: flex !important;
        align-items: center;
    }
}
