.sharing-content{
    text-align: center;

    .heading{
        font-weight: 700;
        font-size: 4rem;
        color:#414141;
        padding-top: 1rem;
        padding-bottom: 1rem;
    }

    .text {
        font-size: 2rem;
        font-weight: 400;
        color: #888;
    }

    .btn-wrapper{
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top:5%;

        .btn{
            display: flex;
            flex-direction: row;
            border-radius: 1.3rem;
            border:0.5px solid #888;
            padding:0.4rem 1.5rem;
            margin-left: 1.5rem;
            margin-right: 1.5rem;
            align-items: center;
            cursor: pointer;
            color: #888;
            transition: all .3s ease-in-out;
            -moz-transition: all .3s ease-in-out;
            -webkit-transition: all .3s ease-in-out;

            div{
                font-size: 2rem;
                font-weight: 700;
                margin-left: 1rem;
            }
            i{
                font-size: 3rem;
            }

            &.android:hover{
                color:#fff !important;
                background: rgba(141,155,70,1);
                background: -moz-linear-gradient(left, rgba(141,155,70,1) 0%, rgba(99,117,11,1) 100%);
                background: -webkit-gradient(left top, right top, color-stop(0%, rgba(141,155,70,1)), color-stop(100%, rgba(99,117,11,1)));
                background: -webkit-linear-gradient(left, rgba(141,155,70,1) 0%, rgba(99,117,11,1) 100%);
                background: -o-linear-gradient(left, rgba(141,155,70,1) 0%, rgba(99,117,11,1) 100%);
                background: -ms-linear-gradient(left, rgba(141,155,70,1) 0%, rgba(99,117,11,1) 100%);
                background: linear-gradient(to right, rgba(141,155,70,1) 0%, rgba(99,117,11,1) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8d9b46', endColorstr='#63750b', GradientType=1 );
                // shadow
                -webkit-box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
                box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
            }

            &.ios:hover{
                color:#fff !important;
                background-image: linear-gradient(-90deg, #212121, #555555);
                // shadow
                -webkit-box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
                box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
            }
        }
        a{
          text-decoration: none;
        }

    }
}
