/*
rem sometimes failed in some browser
media query should be in em
$breakpoint arg
*/
@mixin respond($breakpoint){
    /*
        conditions are written "first desktop manner" as max-width is given
    */
    @if $breakpoint == big-desktop {
        @media (max-width:112.5em){ @content }; /*1800px*/
    }
    @if $breakpoint == tab-land {
        @media (max-width:75em){ @content }; /*1200px*/
    }
    @if $breakpoint == tab-port {
        @media (max-width:56.25em){ @content }; /*900px*/
    }
    @if $breakpoint == phone {
        @media (max-width:37.5em){ @content }; /* 600px */
    }
}

@mixin box-shadow($value, $inset: false) {
    @if $inset {
      -webkit-box-shadow:inset $value;
      -moz-box-shadow:inset $value;
      box-shadow:inset $value;
    } @else {
      -webkit-box-shadow: $value;
      -moz-box-shadow: $value;
      box-shadow: $value;
    }
  }

//box sizing

@mixin box-sizing($value:border-box) {
    -webkit-box-sizing:$value;
	-moz-box-sizing:$value;
	box-sizing:$value;
}