@import "../../styles/mixins";

$navbar-height: 64px !default;
$navbar-line-height: $navbar-height !default;
$navbar-height-mobile: 56px !default;
$navbar-line-height-mobile: $navbar-height-mobile !default;
$navbar-font-size: 1rem !default;
$navbar-font-color: #fff !default;
$navbar-brand-font-size: 2.1rem !default;

.nav {
    display:block;
    position: fixed;
    top:0;
    left:0;
    right: 0;
    height: $navbar-height;
    background-color: teal;
    z-index: 997;
    transition: all .5s ease-in-out;
    -moz-transition: all .5s ease-in-out;
    -webkit-transition: all .5s ease-in-out;

    @include respond(big-desktop){
        height: $navbar-height;
    }
    @include respond(tab-land){
        height: $navbar-height;
    }
    @include respond(tab-port){
        height: $navbar-height-mobile;
    }

    // manages visibility of extract component as two text content components are added
    @media only screen and (max-width: 600px) {
        .hide-on-small-and-down {
            display: none !important;
        }
    }

    &.transparent{
        background-color: transparent;
    }

    &.fill{
        background-color: teal;
        -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
        color: $navbar-font-color;
    }


    .nav-block{
        display: block;
        color: white;
        line-height: $navbar-height;
        max-width: 1500px;
        margin:0 auto;

        @include respond(big-desktop){
            line-height: $navbar-height;
        }
        @include respond(tab-land){
            line-height: $navbar-line-height;
        }
        @include respond(tab-port){
            line-height: $navbar-line-height-mobile;
        }

        .content-wrapper {
            display: flex;
            flex:1;
            flex-direction: row;
            justify-content: space-between;
            font-size: $navbar-brand-font-size;
            padding-left:1rem;
            padding-right:1rem;
            font-weight: 700;

            a {
                color: white;
            }
            .icon-wrapper{
                font-size: 3rem;

                i{
                    padding:1rem;
                    cursor: pointer;
                }
            }
        }

    }
}
