@import "../LandingPage/styles/mixins";

$navbar-height: 120px !default;
$navbar-height-mobile: 72px !default;
$footer-margin :250px;
$footer-margin-mobile :200px;

.details-section-content{
    max-width: 1500px;
    margin-top: 200 * (2 / 3);
    padding-bottom: $footer-margin;

    @include respond(big-desktop){
         margin-top: $navbar-height;
    }
    @include respond(tab-land){
         margin-top: $navbar-height;
    }
    @include respond(tab-port){
         margin-top: $navbar-height-mobile;
         padding-bottom: $footer-margin;
    }

    @media only screen and (max-width: 600px) {
        .details-content {
            visibility: hidden;
            display: none;
        }
    }

    .image-wrapper {
        width: 70%;
        height: 70%;
        margin-bottom: 16px;

        @include respond(phone){
            width: 200px;
            height: 200px;

       }
    }


    .price{
        margin-left: 8px;
    }

    .info-text{
        margin:16px 0;
        font-size: 1.2rem;
        color: #212121;
        background-color: #E0F7FA;
        padding:16px;
        border-radius: 16px;
    }

    .navigation-button{
        width: 100%;
        margin:16px;
        padding:16px 16px;
        border-radius: 8px;
        border: 0;
        outline: none;
        font-size: 1.5rem;
        background-color: darken($color: teal, $amount: 3);
        color: white;
        font-weight: bold;
        cursor: pointer;
    }

    .dm-button{
        width: 100%;
        margin:16px;
        padding:16px 16px;
        border-radius: 8px;
        border: 0;
        outline: none;
        font-size: 1.5rem;
        background-color: darken($color: teal, $amount: 3);
        color: white;
        font-weight: bold;
        cursor: pointer;
    }

    .details-content{
        padding:0px 16px
    }

    .description {
        font-style: 1.2rem;
        font-weight: bold;
    }

    .card{
        margin: 16px;
        padding:16px;
        background-color: #fafafa;
        border-radius:8px;
        @include box-shadow(2px 2px 2px #bdbdbd)
    }

    .details-list{
        font-size: 1.5rem;
        color: #888888;

        li {
            text-decoration: none;
        }
    }
}

.bold {
    font-weight: bold;
}

.large {
    font-size: 2rem;
    color:#212121;
}

.italic {
    font-size: 1.5rem;
    font-style: italic;
}


.footer-wrapper{
    // position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
}

.errImageWrapper{
    display: flex;
    flex:1;
    align-items:center;
    justify-content:center;


    .errImg{
        width: 40%;
        height: auto;

        @include respond(tab-port){
            overflow: hidden !important;
            width: 100%;
            height: auto;
        }
    }
}
