@import '../styles/mixins';

.footer{
    background-color: #3f3f3f;
    // position: 'fixed';
    // bottom: 0;
    // left: 0;
    // right: 0;

    .social-icons-wrapper{
        display: flex;
        flex-direction: row;
        justify-content: center;
        transition: all .3s ease-in-out;
        -moz-transition: all .3s ease-in-out;
        -webkit-transition: all .3s ease-in-out;
        padding-top:2%;

        i {
            font-size: 3rem;
            color: #fff;
            padding:1rem;
            cursor: pointer;

            &:hover{
                color: #b2dfdb;
            }
        }
    }

    .contact-text-wrapper{
      padding-top:1%;
      padding-right: 1rem;
      padding-left: 1rem;
      width: 100%;
      text-align: center;
      color:#fff;
      font-size: 1.2rem;
      a {
        margin: 0 3px;
        color: #999 !important;
      }
    }

    .legal-text-wrapper{
        padding-top:2%;
        padding-bottom:2.5%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        color:#fff;
        font-size: 1.5rem;

        @include respond(tab-port){
            padding-bottom:7%;
        }

        div{
            font-weight: 400;
            cursor: pointer;
            margin:0 0.5rem;

            &:hover{
                transition: all .3s ease-in-out;
                -moz-transition: all .3s ease-in-out;
                -webkit-transition: all .3s ease-in-out;
                text-decoration: underline;
            }
        }
    }
}
