@import './LandingPage/styles/mixins';

// Color
@import "./LandingPage/styles/color-variables";
@import "./LandingPage/styles/color-classes";

@import "./LandingPage/styles/variables";
@import './LandingPage/styles/grid';


html {
  font-size: 62.5%; 
  scroll-behavior: smooth;
  
  @include respond(big-desktop){
      font-size: 75%;
  }
  @include respond(tab-land){
      font-size: 56.25%;
  }
  @include respond(tab-port){
    font-size: 50%;
  }
}

body {
  margin: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg{
  background-color: black
}